<template>
  <div class="email-form">
    <h1>About Us</h1>
    <form @submit.prevent="sendEmail">
      <label for="email">Email:</label>
      <input type="email" id="email" v-model="email" required>
      <br>
      <label for="subject">Subject:</label>
      <input type="text" id="subject" v-model="subject" required>
      <br>
      <label for="message">Message:</label>
      <textarea id="message" v-model="message" required></textarea>
      <br>
      <button type="submit">Send Email</button>
    </form>
    <div v-if="submitForm">Email sent successfully!</div>
    <div v-if="errorMessage">{{ errorMessage }}</div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      email: '',
      subject: '',
      message: '',
      emailSent: false,
      errorMessage: ''
    }
  },
  methods: {
    async submitForm () {
      const params = {
        email: this.email,
        subject: this.subject,
        message: this.message,
        sheet_name: 'contactus'
      }
      const url = 'https://script.google.com/macros/s/AKfycbyfIAbqO2IU6HtHGUxn_c4VP-bN1vw9e65H6jKBt7Y6rBlQAw7anfJEKVlNSoNnXbA7/exec'
      axios.post(url, params)
        .then(response => {
          console.log('message sent:', response.data)
          this.emailSent = true
          this.email = ''
          this.subject = ''
          this.message = ''
        })
        .catch(error => {
          console.error('Error sending message:', error)
          this.errorMessage = 'Failed to send message. Please try again later.'
        })
    }
  }
}
</script>

<style scoped>
.email-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
}

form {
  display: grid;
  gap: 10px;
}

label {
  font-weight: bold;
}

input, textarea {
  width: 100%;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049;
}
</style>
